.Orphanage {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.Orphanage__wrapper {
  flex: 1;
}

.Orphanage__actions {
  display: flex;
  flex: 0 1 auto;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.Orphanage__title {
  text-transform: uppercase;
  justify-content: center;
}
