.Orphanage{display:flex;flex-direction:column;height:100%}.Orphanage__wrapper{flex:1 1}.Orphanage__actions{display:flex;flex:0 1 auto;justify-content:center;align-items:center;margin:1rem 0}.Orphanage__title{text-transform:uppercase;justify-content:center}

.Workspace{position:relative;height:100%;overflow:hidden}.Workspace__Graph-wrapper{position:relative;height:100%}

.App{position:absolute;top:0;right:0;left:0;bottom:0;overflow:hidden}

*{padding:0;margin:0;box-sizing:border-box}html{height:100%}body{margin:0;height:100%;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{font:inherit;text-transform:inherit;background:transparent;border:0;outline:0;border-radius:0}input,textarea{font:inherit;background:transparent;border:0;outline:0}a{text-decoration:none;outline:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

